<template>
  <div
    v-if="isActiveModal"
    class="dialog-wrap">
    <div class="overlay-scrim"></div>

    <div class="dialog-container">
      <div class="dialog-content">
        <div class="dialog-header">
          <h1></h1>
          <button
            type="button"
            class="header-btnClose"
            @click="modalClose">
            <span class="blind"></span>
          </button>
        </div>

        <div class="dialog-body">
          <div class="mo-search">
            <ul>
              <li>
                <label for=""><input type="checkbox" class="checkbox" checked="checked" v-if="checkboxShow"></label>
                <v-select
                :items="items"
                class="selectBox"
                label=""
                dense
                solo />
              </li>
              <li>
                <label for=""><input type="checkbox" class="checkbox" checked="checked"  v-if="checkboxShow"></label>
                <v-select
                  :items="items"
                  class="selectBox"
                  label=""
                  dense
                  solo />
              </li>
              <li>
                <label for=""><input type="checkbox" class="checkbox" checked="checked" v-if="checkboxShow"></label>
                <v-select
                  :items="items"
                  class="selectBox"
                  label=""
                  dense
                  solo />
              </li>
              <li>
                <label for=""><input type="checkbox" class="checkbox" checked="checked" v-if="checkboxShow"></label>
                <v-select
                  :items="items"
                  class="selectBox"
                  label=""
                  dense
                  solo />
              </li>
              <li>
                <label for=""><input type="checkbox" class="checkbox" checked="checked" v-if="checkboxShow"></label>
                <date-picker />
              </li>
              <li>
                <label for=""><input type="checkbox" class="checkbox" checked="checked" v-if="checkboxShow"></label>
                <date-picker />
              </li>
              <li>
                <label for=""><input type="checkbox" class="checkbox" checked="checked" v-if="checkboxShow">ID</label>
                <input type="text" class="form-control" placeholder="ID  .">
              </li>
              <li>
                <label for=""><input type="checkbox" checked="checked" v-if="checkboxShow">ID</label>
                <input type="text" class="form-control" placeholder="ID  .">
              </li>
              <li>
                <label for=""><input type="checkbox" checked="checked" v-if="checkboxShow">Combobox</label>
                <v-combobox
                  v-model="model"
                  :items="items"
                  class="multiCombox"
                  label=" ."
                  hide-selected
                  multiple
                  small-chips
                  solo >
                  <template v-slot:selection="{ item, parent}">
                    <v-chip
                      label >
                      <span>
                        {{ item }}
                      </span>
                      <v-icon
                        small
                        @click="parent.selectItem(item)" >
                        $delete
                      </v-icon>
                    </v-chip>
                  </template>
                </v-combobox> 
              </li>
            </ul>
          </div>
          <div class="diglog-btnArea mt-5">
            <btn-reset-search 
              v-if="searchOption" />
            <btn-download-search 
              v-else />
          </div>  
        </div>

        <div class="dialog-footer">
          <button
            type="button"
            class="btn-modalClose"
            @click="modalClose">
            
          </button>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import BtnResetSearch from '@/components/BtnResetSearch'
import BtnDownloadSearch from '@/components/moSearch/BtnDownloadSearch.vue'
import DatePicker from '@/components/DatePicker'

import { mapState } from 'vuex'

export default {
  components: {
    BtnResetSearch,
    BtnDownloadSearch,
    DatePicker
  },
  props: {
    items: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    ...mapState([
      'isActiveModal',
      'searchOption',
      'checkboxShow'
    ])
  },
  methods: {
    modalClose() {
      this.$store.commit('MODAL_CLOSE')
    }
  }
}
</script>

